import React, { Component } from 'react';
import moment from 'moment';
import { connect } from 'react-redux';
import globalStyles from '../../common/style';
import Medium from "../../assets/png/medium.png";
import { getPosts } from '../../modules/blogs';
import { toggleSendMessageModal } from '../../modules/static/modals';
class Blogs extends Component {
    state = {
        loaded: false
    }
    constructor(props) {
        super(props);
        this.renderFetchBlogs();
    }
    componentDidMount() {
        this.setState({
            loaded: true
        })
    }
    renderFetchBlogs = () => {
        if (this.props.posts.length === 0 && this.props.fetching === false) {
            this.props.getPosts()
        }
        setTimeout((() => {
            this.setState({ loaded: true })
        }).bind(this), 1000)
    }
    renderOnePost(item = {}) {
        return (
            <div className="col-lg-4 justify-content-center d-flex align-items-center" style={{ maxWidth: "400px" }}>
                <div className="card blog-card" style={{ margin: "20px 10px" }}>
                    <img onClick={() => window.open(item?.link, "__blank")}
                        src={item?.thumbnail || "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"}
                        onError={(e) => {
                            e.target.src = "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"
                        }}
                        className="card-img-top"
                        width="100%"
                        height="220px"
                        style={{
                            objectFit: "cover"
                        }}
                        alt={item?.title.replace(/(&amp;)/ig, " & ")} />
                    <div className="card-body blog-body">
                        <p className="blog-information d-flex align-items-center justify-content-center">
                            <span style={{ flex: 3 }}>Yazar:
                                <a className="blog-author"
                                    style={{
                                        color: globalStyles.primaryColor,
                                        fontFamily: 'inherit',
                                        fontWeight: 'bold',
                                        marginLeft: "10px"
                                    }}
                                    target="__blank"
                                    href={this.props.author?.link}>
                                    {item?.author}
                                </a>
                            </span>
                            <span className="align-items-start justify-content-end blog-pub-date"
                                style={{
                                    flex: 2,
                                    textAlign: "right",
                                    letterSpacing: "-0.5px"
                                }}>
                                {item?.pubDate && moment(item?.pubDate).format('LL')}
                            </span>
                        </p>
                        <h4 className="card-title blog-title" style={{
                            cursor: "pointer"
                        }} onClick={() => window.open(item?.link, "__blank")}>
                            {item?.title?.replace(/(&amp;)/ig, " & ")}
                        </h4>
                        <p className="card-text blog-content" style={{ textAlign: "justify" }}>{item?.description.replace(/(<([^>]+)>)/ig, "").replace(/\"/ig, "").replace(/(&amp;)/ig, " & ").split(/\n/).slice(2).join("\n")}</p>
                        {/* <hr style={{ maxHeight: "5px", margin: "0px 0px 0px 5px" }} /> */}
                        <div style={{ height: "3px" }}></div>
                        {/* <button className="readBtn" onClick={() => window.open(item?.link, "__blank")} target="__blank">Devamını Oku</button> */}
                    </div>
                </div>
            </div>
        )
    }
    renderOnePostMedium(item = {}) {
        return (
            <div className="col-lg-4 justify-content-center d-flex align-items-center" style={{ maxWidth: "400px" }}>
                <div className="card blog-card" style={{ margin: "20px 10px" }}>
                    <img onClick={() => window.open("https://cangokceaslan.medium.com/", "__blank")}
                        src={Medium}
                        onError={(e) => {
                            e.target.src = "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"
                        }}
                        className="card-img-top"
                        width="100%"
                        height="220px"
                        style={{
                            objectFit: "cover"
                        }}
                        alt={"Medium hesabıma erişerek yazılarıma ulaşabilirsin"} />
                    <div className="card-body blog-body">
                        <p className="blog-information d-flex align-items-center justify-content-center">
                            <span style={{ flex: 3 }}>Yazar:
                                <a className="blog-author"
                                    style={{
                                        color: globalStyles.primaryColor,
                                        fontFamily: 'inherit',
                                        fontWeight: 'bold',
                                        marginLeft: "10px"
                                    }}
                                    target="__blank"
                                    href={"https://cangokceaslan.medium.com/"}>
                                    {"Can Gökçeaslan"}
                                </a>
                            </span>
                            <span className="align-items-start justify-content-end blog-pub-date"
                                style={{
                                    flex: 2,
                                    textAlign: "right",
                                    letterSpacing: "-0.5px"
                                }}>
                                {moment(new Date().toString()).format('LL')}
                            </span>
                        </p>
                        <h4 className="card-title blog-title" style={{
                            cursor: "pointer"
                        }} onClick={() => window.open("https://cangokceaslan.medium.com", "__blank")}>
                            Medium hesabıma erişerek son postlarımdan haberdar olun
                        </h4>
                        <p className="card-text blog-content" style={{ textAlign: "justify" }}>Medium hesabımı ziyaret ederek özellikle yazılım ve teknoloji üzerine paylaşmış olduğum yazılara ulaşabilirsiniz</p>
                        {/* <hr style={{ maxHeight: "5px", margin: "0px 0px 0px 5px" }} /> */}
                        <div style={{ height: "3px" }}></div>
                        {/* <button className="readBtn" onClick={() => window.open(item?.link, "__blank")} target="__blank">Devamını Oku</button> */}
                    </div>
                </div>
            </div>
        )
    }
    render() {
        return (
            <section className="blog" id="blog">
                <h2 className="blog-header"><img src="assets/svg/medium-sqr.svg" width="40px" height="37px" style={{ marginRight: "12px" }} /> <img src="assets/svg/medium-long.svg" width="150px" height="37px" style={{ marginRight: "12px" }} /></h2>
                <div className="container-fluid top-container">
                    <div className="blog-container">
                        <div className="container">
                            <div className="row justify-content-center d-flex align-items-center">
                                {(this.props.posts || []).map((item, index) => this.renderOnePost(item))}
                                {this.renderOnePostMedium({})}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default connect((state) => ({
    posts: state?.blogs?.posts,
    author: state?.blogs?.author,
    fetching: state?.blogs?.fetching
}), {
    getPosts,
    toggleSendMessageModal
})(Blogs);